import React from 'react';
import EnterData from './EnterData/EnterData';
import TodoItem from './TodoItem/TodoItem';
import classes from './Todos.module.css';
import FilterData from './FilterData/FilterData';
const Todos = (props) => {
  console.log(props.filterTodos);
  return (
    <div className='container'>
      <EnterData {...props} />
      <FilterData
        filter={props.filter}
        filterText={props.filterText}
        canFilter={props.canFilter}
      />
      <div className={classes.TodoItems}>
        {Object.keys(props.filterTodos).length === 0
          ? Object.keys(props.todos).map((todo, i) => (
              <TodoItem
                key={todo}
                todo={props.todos[todo]}
                remove={() => props.removed(todo)}
              />
            ))
          : props.filterTodos['noTodos']
          ? null
          : Object.keys(props.filterTodos).map((todo, i) => (
              <TodoItem
                key={todo}
                todo={props.todos[todo]}
                remove={() => props.removed(todo)}
              />
            ))}
      </div>
    </div>
  );
};

export default Todos;
