import React, { Component } from 'react';
import Navbar from '../components/Navbar/Navbar';
import Todos from '../components/Todos/Todos';
class App extends Component {
  state = {
    todos: {},
    text: '',
    filterTodos: {},
    filterText: '',
    canFilter: false,
  };
  filterTodos = (e) => {
    const data = e.target.value;
    if (data.length !== 0) {
      const reg = new RegExp(data);
      let todos = {};
      // const todos = {...this.state.todos}
      Object.keys(this.state.todos).forEach((key) => {
        if (reg.test(this.state.todos[key])) {
          todos[key] = this.state.todos[key];
        }
      });
      if (Object.keys(todos).length !== 0) {
        this.setState({ filterTodos: todos, filterText: e.target.value });
      } else {
        this.setState({
          filterTodos: { noTodos: true },
          filterText: e.target.value,
        });
      }
    } else {
      this.setState({
        filterTodos: {},
        filterText: e.target.value,
      });
    }
  };
  onChange = (e) => {
    this.setState({ text: e.target.value });
  };
  addTodo = async (e) => {
    e.preventDefault();
    if (this.state.text !== '') {
      let todos = { ...this.state.todos };
      todos[Date.now()] = this.state.text;
      this.setState({
        todos: todos,
        text: '',
        canFilter: true,
      });
    }
  };
  removeTodo = (index) => {
    const todos = { ...this.state.todos };
    const filterTodos = { ...this.state.filterTodos };
    let canFilter = true;
    let filterText = this.state.filterText;
    delete todos[index];
    delete filterTodos[index];
    if (Object.keys(filterTodos).length === 0) {
      filterText = '';
    }
    if (Object.keys(todos).length === 0) {
      canFilter = false;
    }

    this.setState({
      todos: todos,
      filterTodos: filterTodos,
      filterText: filterText,
      canFilter: canFilter,
    });
  };
  render() {
    return (
      <>
        <Navbar todosLength={Object.keys(this.state.todos).length} />
        <Todos
          click={this.addTodo}
          changed={this.onChange}
          todos={this.state.todos}
          filterTodos={this.state.filterTodos}
          removed={this.removeTodo}
          text={this.state.text}
          filter={this.filterTodos}
          filterText={this.state.filterText}
          canFilter={this.state.canFilter}
        />
      </>
    );
  }
}

export default App;
